<template>
    <div>
        <geometry-element
                :selectable="movingElement"
                :movable="!isRead && movingElement"
                :resizable="!isRead && movingElement"
                :deletable=!isRead
                connectable
                :angle.sync="value.elementView.angle"
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                :customMoveActionExist="isCustomMoveActionExist"
                v-on:customMoveAction="delayedMove"

                v-on:dblclick="showProperty"
                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:addedToGroup="onAddedToGroup"
                v-on:removeShape="onRemoveShape(value)"
                :label.sync="namePanel"
                :image.sync="setImg"
                :_style="{
                'label-angle':value.elementView.angle,
                'font-weight': 'bold','font-size': '16'
                }"
        >
            <!--v-on:dblclick="$refs['dialog'].open()"-->
            <geometry-rect
                    v-if="movingElement"
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#5099F7',
          fill: '#5099F7',
          'fill-opacity': 1,
          r: '1', 'z-index': '998'
        }"
            >
            </geometry-rect>
            <geometry-rect
                    v-else
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#5099F7',
          fill: '#5099F7',
          'fill-opacity': .5,
          r: '1', 'z-index': '998'
        }"
            >
            </geometry-rect>
            <sub-elements v-for="(index) in newEditUserImg.length">
                <image-element
                        v-bind:image="newEditUserImg[index-1].picture"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="(10*(index-1))+'px'"
                        :sub-bottom="value.elementView.height"
                >
                </image-element>
            </sub-elements>

            <sub-elements>
                <!--title-->
                <text-element
                        :sub-width="'100%'"
                        :sub-height="titleH"
                        :sub-top="0"
                        :sub-left="0"
                        :text="value.classReference ? value.classReference : '<< Command >>'">
                </text-element>
                <image-element
                        v-if="alertImage"
                        v-bind:image="failed_image"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="'5px'"
                        :sub-bottom="'5px'"
                >
                </image-element>
            </sub-elements>
        </geometry-element>

        <!--        <modeling-property-panel-->
        <!--                v-model="value"-->
        <!--                :titleName="'Command'"-->
        <!--                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/command.png'"-->
        <!--        >-->
        <!--        </modeling-property-panel>-->


        <modeling-property-panel
                v-if="openPanel"
                v-model="value"
                :titleName="'Command'"
                :errorMsg="errorMsg"
                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/command.png'"
        >
        </modeling-property-panel>


    </div>
</template>

<script>
    import Element from '../../modeling/Element'

    var changeCase = require('change-case');
    var pluralize = require('pluralize');

    var Mustache = require('mustache')

    export default {
        mixins: [Element],
        name: 'command-definition',
        props: {},
        computed: {
            errorMsg() {
                var me = this
                if (me.namePanel == '' && Object.keys(me.value.aggregate).length == 0 && Object.keys(me.value.boundedContext).length == 0) {
                    return 'Element must be in a Bounded Context, Must be attached to an Aggregate and Must has a name.'
                } else if (Object.keys(me.value.aggregate).length == 0) {
                    return 'Must be attached to an Aggregate.'
                } else if (Object.keys(me.value.boundedContext).length == 0) {
                    return 'Element must be in a Bounded Context.'
                } else if (me.namePanel == '') {
                    return 'Must has a name.'
                } else {
                    return ''
                }
            },
            alertImage() {
                var me = this
                if (Object.keys(me.value.aggregate).length == 0 || me.namePanel == '' || Object.keys(me.value.boundedContext).length == 0) {
                    return true
                } else {
                    return false
                }
            },
            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.modeling.model.Command'
            },
            createNew(elementId, x, y, width, height) {
                return {
                    _type: this.className(),
                    name: '',
                    namePlural: '',
                    namePascalCase: '',
                    nameCamelCase: '',

                    aggregate: {},
                    boundedContext: {},

                    // alertURL: location.pathname + ((location.pathname == '/' || location.pathname.lastIndexOf('/') > 0) ? '' : '/') + 'static/image/symbol/alert-icon.png',
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': width,
                        'height': height,
                        'style': JSON.stringify({}),
                        'z-index': 999
                    },
                    //new pannel
                    isRestRepository: true,
                    controllerInfo: {
                        apiPath: '',
                        method: 'GET'
                    },
                    restRepositoryInfo: {
                        method: 'GET'
                    },
                    relationEventInfo: [],

                    // drawer: false,
                    // selected: false,
                    // editing: false,
                }
            }
        },
        data: function () {
            return {
                isRead: false,
                itemH: 20,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference,
                failed_image: location.pathname + ((location.pathname == '/' || location.pathname.lastIndexOf('/') > 0) ? '' : '/') + 'static/image/symbol/alert-icon.png',
                isValueChanged: false,
                newEditUserImg: [],
                setImg: '',
            };
        },
        created: function () {
            var me = this
            var designer = me.getComponent('modeling-designer')

            this.$nextTick(function () {
                if (this.namePanel != null) {
                    this.namePanel = me.value.name
                }

                me.isRead = designer.isRead
            })
        },
        watch: {
            "movingElement": function () {
                this.refreshImg()
            },
            "alertImage": function () {
                this.refreshImg()
            },
            "newEditUserImg": function (newVal) {
                var me = this
                me.$nextTick(function () {
                    if (newVal.length != 0) {
                        this.setImg = newVal[newVal.length - 1].picture
                    } else {
                        this.setImg = ''
                    }

                })
            },
            "value.name": function (newVal, oldVal) {
                this.namePanel = newVal
            },
            // "value.aggregate": {
            //     deep: true,
            //     handler: function (newVal, oldVal) {
            //         var me = this
            //         if (Object.keys(newVal).length == 0) {
            //             //delete agg
            //         } else {
            //             if (newVal.id == oldVal.id) {
            //                 // console.log("Equal")
            //             } else {
            //                 // console.log("no Equal")
            //                 var addObj = {
            //                     state: "addCommand",
            //                     element: JSON.parse(JSON.stringify(me.value))
            //                 }
            //                 var deleteObj = {
            //                     state: "deleteCommand",
            //                     element: JSON.parse(JSON.stringify(me.value))
            //                 }
            //
            //                 if (oldVal.id != null) {
            //                     me.$EventBus.$emit(`${oldVal.id}`, deleteObj)
            //                 }
            //
            //                 me.$EventBus.$emit(`${newVal.id}`, addObj)
            //
            //             }
            //         }
            //     }
            // },
            "openPanel": function (newVal) {
                var me = this
                if (newVal == false) {

                    // me.setNameCase(me.namePanel)

                    if (me.isValueChanged) {
                        var obj = {
                            state: 'updateCommand',
                            element: JSON.parse(JSON.stringify(me.value))
                        }

                        console.log('updateCommand', obj.element)
                        //update agg
                        if (Object.keys(me.value.aggregate).length > 0) {
                            me.$EventBus.$emit(`${me.value.aggregate.id}`, obj)
                        }

                        //update conn Event
                        if (me.value.relationEventInfo.length > 0) {
                            me.value.relationEventInfo.forEach(function (eventInfo) {
                                me.$EventBus.$emit(eventInfo.eventValue.elementView.id, obj)
                            })
                        }

                        me.isValueChanged = false
                    } else {

                    }

                }

            },
        },
        mounted: function () {
            var me = this
            var designer = me.getComponent('modeling-designer')

            me.$nextTick(function () {
                me.isRead = designer.isRead
            })

            this.$EventBus.$on(`${me.value.elementView.id}`, function (obj) {


                if (obj.state == 'updateEvent') {
                    //value .event update

                    var targetId = obj.element.elementView.id
                    var findEvIndex = me.value.relationEventInfo.findIndex(ev => ev.eventValue.elementView.id == targetId)

                    if (findEvIndex != -1) {
                        me.value.relationEventInfo[findEvIndex].eventValue = obj.element;

                        //agg update
                        var updateObj = {
                            state: "updateCommand",
                            element: JSON.parse(JSON.stringify(me.value))
                        }

                        //event   .
                        me.$EventBus.$emit(`${me.value.relationEventInfo[findEvIndex].eventValue.elementView.id}`, updateObj)

                    }

                } else if (obj.state == 'updateBounded') {

                    console.log('updateBounded: updateBounded')
                    me.value.boundedContext.name = obj.element.name;
                    me.value.boundedContext.id = obj.element.elementView.id

                    var updateObj = {
                        state: 'updateCommand',
                        element: JSON.parse(JSON.stringify(me.value))
                    }

                    if (Object.keys(me.value.aggregate).length > 0) {
                        me.$EventBus.$emit(`${me.value.aggregate.id}`, updateObj)
                    }

                    if(me.value.relationEventInfo.length>0){
                        me.value.relationEventInfo.forEach(function (eventInfo) {
                            me.$EventBus.$emit(`${eventInfo.eventValue.elementView.id}`, updateObj)
                        })
                    }

                } else if (obj.state == "deleteAggregate") {
                    me.value.aggregate = {}

                    // if (obj.type == "org.uengine.modeling.model.Aggregate") {
                    //     me.value.aggregate = {}
                    // }
                } else if (obj.state == 'updateAggregate') {

                    var tmpAgg = {
                        'id': obj.element.elementView.id,
                        'name': obj.element.name,
                        'namePlural': obj.element.namePlural,
                        'namePascalCase': obj.element.namePascalCase,
                        'nameCamelCase': obj.element.nameCamelCase,
                        'fieldDescriptors': obj.element.aggregateRoot.fieldDescriptors,
                        'boundedContext': obj.element.boundedContext,
                    }


                    me.value.aggregate = tmpAgg

                    var updateObj = {
                        state: "updateCommand",
                        element: JSON.parse(JSON.stringify(me.value))
                    }

                    if (Object.keys(me.value.aggregate).length > 0) {
                        me.$EventBus.$emit(`${me.value.aggregate.id}`, updateObj)
                    }


                    if(me.value.relationEventInfo.length>0){
                        me.value.relationEventInfo.forEach(function (eventInfo) {
                            me.$EventBus.$emit(`${eventInfo.eventValue.elementView.id}`, updateObj)
                        })
                    }

                } else if (obj.state == 'deleteRelation') {

                    var targetEventId = obj.element.sourceElement.elementView.id
                    var findEvIndex = me.value.relationEventInfo.findIndex(ev => ev.eventValue.elementView.id == targetId)

                    if (findEvIndex != -1) {
                        me.value.relationEventInfo[findEvIndex] = null
                    }
                    me.value.relationEventInfo = me.value.relationEventInfo.filter(n => n)

                    var updateObj = {
                        state: "updateCommand",
                        element: JSON.parse(JSON.stringify(me.value))
                    }

                    if (Object.keys(me.value.aggregate).length > 0) {
                        me.$EventBus.$emit(`${me.value.aggregate.id}`, updateObj)
                    }

                } else if (obj.state == 'elementMove') {
                    me.movingElement = true
                    var afterMove = JSON.parse(obj.element)
                    me.value.elementView.x = afterMove.x
                    me.value.elementView.y = afterMove.y
                    me.value.elementView.width = afterMove.width
                    me.value.elementView.height = afterMove.height


                }

                if (obj.state == 'editUserPanelOpen' || obj.state == 'editUserSelectOn') {

                    if (me.newEditUserImg.findIndex(user => user.uid == obj.uid) == -1) {
                        me.newEditUserImg.push(obj)
                    }

                } else if (obj.state == 'editUserPanelClose' || obj.state == 'editUserSelectOff') {
                    if (me.newEditUserImg.length > 0) {
                        me.newEditUserImg.forEach(function (user, idx) {
                            if (user.uid == obj.uid) {
                                me.newEditUserImg[idx] = null
                            }
                        })
                        me.newEditUserImg = me.newEditUserImg.filter(n => n)
                    }
                }
            })
        },
        methods: {
            refreshImg() {
                var me = this
                var origin = JSON.parse(JSON.stringify(this.setImg))
                this.setImg = 'test'
                me.$nextTick(function () {
                    if (this.setImg == 'test') {
                        this.setImg = ''
                    } else {
                        this.setImg = 'test'
                    }
                })
            },
            setNameCase:
                function (newVal) {
                    var me = this
                    me.value.name = newVal
                    me.value.namePascalCase = changeCase.pascalCase(newVal)
                    me.value.nameCamelCase = changeCase.camelCase(newVal)
                    me.value.namePlural = changeCase.camelCase(pluralize(newVal));
                },
            deleteCommand() {
                var me = this
                return new Promise(function (resolve) {
                    me.$EventBus.$off(`${me.value.elementView.id}`);

                    var obj = {
                        state: "deleteCommand",
                        element: JSON.parse(JSON.stringify(me.value))
                    }

                    //aggregate notify
                    if (me.value.aggregate.id != null) {
                        me.$EventBus.$emit(me.value.aggregate.id, obj)
                    } else if (me.value.boundedContext.id != null) {
                        me.$EventBus.$emit(me.value.boundedContext.id, obj)
                    }

                    if (me.value.relationEventInfo.length > 0) {
                        me.value.relationEventInfo.forEach(function (CoItem) {
                            if (CoItem.relationId != null) {
                                //relation
                                me.$EventBus.$emit(`${CoItem.relationId}`, obj)

                            }

                        })
                    }

                    me.$emit('update:value', null);
                    resolve()
                })
            }

        }
    }
</script>


