<template>
    <div>
        <group-element
                :selectable="movingElement"
                :movable="!isRead && movingElement"
                :resizable="!isRead && movingElement"
                :deletable=!isRead
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :label.sync="namePanel"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                :customMoveActionExist="isCustomMoveActionExist"
                v-on:customMoveAction="delayedMove"

                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:dblclick="showProperty"
                v-on:addToGroup="onAddToGroup"
                v-on:removeShape="onRemoveShape(value)"
                :image.sync="setImg"
                :_style="{
                'vertical-align': 'top',
                'font-weight': 'bold',
                'font-size': '16'
                }"
        >

            <geometry-rect
                    v-if="movingElement"
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#000000',
          'fill-opacity': 1,
          'vertical-align': 'top',
          'font-weight': 'bold',
          'font-size': '16',
          'r': '1'
        }">
            </geometry-rect>

            <geometry-rect
                    v-else
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#000000',
          'fill': '#e3e1e1',
          'fill-opacity': .5,
          'vertical-align': 'top',
          'font-weight': 'bold',
          'font-size': '16',
          'r': '1'
        }">
            </geometry-rect>

            <storming-sub-controller :type="type"></storming-sub-controller>

            <sub-elements v-for="(index) in newEditUserImg.length">
                <image-element
                        v-bind:image="newEditUserImg[index-1].picture"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="(10*(index-1))+'px'"
                        :sub-bottom="value.elementView.height"
                >
                </image-element>
            </sub-elements>

        </group-element>

        <modeling-property-panel
                v-if="openPanel"
                v-model="value"
                :errorMsg="errorMsg"
                titleName="BoundedContext"
        >
        </modeling-property-panel>

    </div>
</template>

<script>
    import Element from '../../modeling/Element'
    import GroupElement from "../../../opengraph/shape/GroupElement";
    import ImageElement from "../../../opengraph/shape/ImageElement";

    var changeCase = require('change-case');
    var pluralize = require('pluralize');


    export default {
        components: {ImageElement, GroupElement},
        mixins: [Element],
        name: 'bounded-context-definition',
        props: {},
        computed: {
            errorMsg() {
                var me = this
                if (me.namePanel == '') {
                    return 'Please input Name.'
                } else {
                    return ''
                }
            },
            alertImage() {
                var me = this
                if (me.namePanel == '') {
                    return true
                } else {
                    return false
                }
            },
            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.modeling.model.BoundedContext'
            },
            createNew(elementId, x, y, width, height) {
                return {
                    _type: this.className(),
                    name: 'BoundedContext' + x,
                    namePlural: '',
                    aggregates: [],
                    policies: [],
                    viewes: [],
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': 300,
                        'height': 300,
                        'style': JSON.stringify({})
                    },
                    portGenerated: 0,
                    groups: [],
                    tempId: ''
                }
            }
        },
        data: function () {
            return {
                isRead: false,
                itemH: 20,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference,
                isValueChanged: false,
                newEditUserImg: [],
                setImg: '',
            };
        },
        created: function () {
            var me = this
            var designer = me.getComponent('modeling-designer')

            this.$nextTick(function () {
                if (this.namePanel != null) {
                    this.namePanel = me.value.name
                }

                me.isRead = designer.isRead
            })
        },
        watch: {
            "movingElement": function () {
                this.refreshImg()
            },
            "alertImage": function () {
                this.refreshImg()
            },
            "newEditUserImg": function (newVal) {
                var me = this
                me.$nextTick(function () {
                    if (newVal.length != 0) {
                        this.setImg = newVal[newVal.length - 1].picture
                    } else {
                        this.setImg = ''
                    }

                })
            },
            // "value.selected": function (newVal) {
            //     var me = this
            //     // if(newVal){
            //     //     this.value.groups.forEach(function (ele) {
            //     //         var obj={
            //     //            state: 'updateBounded',
            //     //             element: me.value
            //     //         }
            //     //         me.$EventBus.$emit(`${ele}`,obj)
            //     //     })
            //     // }
            // },
            // 'value.name': function (newVal, oldVal) {
            //     var me = this
            //     me.value.checkValue = true
            // },
            "value.name": function (newVal, oldVal) {
                this.namePanel = newVal
                this.isValueChanged = true
            },
            "openPanel": function (newVal) {
                var me = this
                if (newVal == false) {

                    me.$nextTick(function () {
                        if (me.isValueChanged) {
                            me.value.name = me.namePanel

                            var updateObj = {
                                state: 'updateBounded',
                                element: JSON.parse(JSON.stringify(me.value))
                            }

                            console.log('openPanel updateBounded',updateObj)
                            me.value.groups.forEach(function (eleId) {
                                me.$EventBus.$emit(`${eleId}`, updateObj)
                            })

                            me.value.aggregates.forEach(function (agg) {
                                me.$EventBus.$emit(agg.elementView.id, updateObj)
                            })
                            me.value.policies.forEach(function (po) {
                                me.$EventBus.$emit(po.elementView.id, updateObj)
                            })
                            me.value.viewes.forEach(function (vi) {
                                me.$EventBus.$emit(vi.elementView.id, updateObj)
                            })

                            me.isValueChanged = false

                        }
                    })

                }

            },


        },
        mounted: function () {
            var me = this
            var boundedId = this.value.elementView.id

            var $tr = $('#' + boundedId);
            $tr.parent().children().first().before($tr)

            var me = this
            this.$EventBus.$on(`${me.value.elementView.id}`, function (obj) {

                if (obj.state == 'updateAggregate') {
                    var targetId = obj.element.elementView.id
                    var findAggIndex = me.value.aggregates.findIndex(agg => agg.elementView.id == targetId)

                    if (findAggIndex != -1) {
                        me.value.aggregates[findAggIndex] = obj.element
                    }

                } else if (obj.state == 'deleteAggregate') {

                    var targetId = obj.element.elementView.id
                    var findAggIndex = me.value.aggregates.findIndex(agg => agg.elementView.id == targetId)
                    var findGroupIndex = me.value.groups.findIndex(id => id == targetId)

                    if (findAggIndex != -1) {
                        me.value.aggregates[findAggIndex] = null
                    }

                    if (findGroupIndex != -1) {
                        me.value.groups[findGroupIndex] = null
                    }

                    me.value.groups = me.value.groups.filter(n => n);
                    me.value.aggregates = me.value.aggregates.filter(n => n);


                } else if (obj.state == 'updatePolicy') {

                    var targetId = obj.element.elementView.id
                    var findAggIndex = me.value.policies.findIndex(po => po.elementView.id == targetId)

                    console.log('updatePolicy:: ', obj)
                    if (findAggIndex != -1) {
                        me.value.policies[findAggIndex] = obj.element
                    } else if (findAggIndex == -1) {
                        me.value.policies.push(obj.element)
                    }

                } else if (obj.state == 'deletePolicy') {

                    var targetId = obj.element.elementView.id
                    var findAggIndex = me.value.policies.findIndex(po => po.elementView.id == targetId)
                    var findGroupIndex = me.value.groups.findIndex(id => id == targetId)

                    if (findAggIndex != -1) {
                        me.value.policies[findAggIndex] = null
                    }

                    if (findGroupIndex != -1) {
                        me.value.groups[findGroupIndex] = null
                    }

                    me.value.policies = me.value.policies.filter(n => n);
                    me.value.groups = me.value.groups.filter(n => n);


                } else if (obj.state == 'deleteEvent') {

                    var targetId = obj.element.elementView.id
                    var findGroupIndex = me.value.groups.findIndex(id => id == targetId)


                    if (findGroupIndex != -1) {
                        me.value.groups[findGroupIndex] = null
                    }

                    me.value.groups = me.value.groups.filter(n => n);


                } else if (obj.state == 'deleteView') {
                    var targetId = obj.element.elementView.id
                    var findViewIndex = me.value.viewes.findIndex(vi => vi.elementView.id == targetId)
                    var findGroupIndex = me.value.groups.findIndex(id => id == targetId)

                    if (findViewIndex != -1) {
                        me.value.viewes[findViewIndex] = null
                    }

                    if (findGroupIndex != -1) {
                        me.value.groups[findGroupIndex] = null
                    }

                    me.value.viewes = me.value.viewes.filter(n => n);
                    me.value.groups = me.value.groups.filter(n => n);

                } else if (obj.state == 'updateView') {

                    console.log('updateView:', obj)
                    var targetId = obj.element.elementView.id
                    var findViewIndex = me.value.viewes.findIndex(vi => vi.elementView.id == targetId)


                    if (findViewIndex != -1) {
                        me.value.viewes[findViewIndex] = obj.element
                    }


                } else if (obj.state == 'deleteCommand') {

                    var targetId = obj.element.elementView.id
                    var findGroupIndex = me.value.groups.findIndex(id => id == targetId)


                    if (findGroupIndex != -1) {
                        me.value.groups[findGroupIndex] = null
                    }

                    me.value.groups = me.value.groups.filter(n => n);


                } else if (obj.state == 'updateCommand') {
                    // var targetId = obj.element.elementView.id
                    // var findGroupIndex = me.value.groups.findIndex(id => id === targetId)
                    //
                    //
                    // if (findGroupIndex != -1) {
                    //     me.value.groups[findGroupIndex] = obj.element
                    // }
                }


                if (obj.state == 'elementMove') {
                    me.movingElement = true
                    var afterMove = JSON.parse(obj.element)
                    me.value.elementView.x = afterMove.x
                    me.value.elementView.y = afterMove.y
                    me.value.elementView.width = afterMove.width
                    me.value.elementView.height = afterMove.height
                }

                if (obj.state == 'editUserPanelOpen' || obj.state == 'editUserSelectOn') {

                    if (me.newEditUserImg.findIndex(user => user.uid == obj.uid) == -1) {
                        me.newEditUserImg.push(obj)
                    }

                } else if (obj.state == 'editUserPanelClose' || obj.state == 'editUserSelectOff') {
                    if (me.newEditUserImg.length > 0) {
                        me.newEditUserImg.forEach(function (user, idx) {
                            if (user.uid == obj.uid) {
                                me.newEditUserImg[idx] = null
                            }
                        })

                        me.newEditUserImg = me.newEditUserImg.filter(n => n)
                    }
                }
            })
        },
        methods: {
            refreshImg() {
                var me = this
                var origin = JSON.parse(JSON.stringify(this.setImg))
                this.setImg = 'initImg'
                me.$nextTick(function () {
                    if (this.setImg == 'initImg') {
                        this.setImg = ''
                    } else {
                        this.setImg = 'initImg'
                    }
                })
            },
            deleteBoundedContext() {
                var me = this
                return new Promise(function (resolve) {
                    me.value.aggregates.forEach(function (aggregate) {
                        console.log(aggregate.elementView.id)
                        var obj = {
                            state: "deleteBounded",
                            element: JSON.parse(JSON.stringify(me.value))
                        }
                        me.$EventBus.$emit(aggregate.elementView.id, obj)
                    })
                    me.deleteInBounded(me.value.name)
                    me.$EventBus.$off(`${me.value.elementView.id}`);
                    me.$emit('update:value', null);
                    // me.$EventBus.$emit('storage')
                    resolve()
                })
            },
            deleteInBounded(boundedName) {
                var me = this
                var designer = this.getComponent('modeling-designer')

                //Array
                designer.value.definition.forEach(function (item, idx) {
                    if (item != null) {
                        if (item.boundedContext && item.boundedContext.name == boundedName) {
                            // console.log("boundedItem Id : ", item.elementView.id)
                            me.$EventBus.$off(`${item.elementView.id}`);
                            designer.value.definition[idx] = null;
                        }
                    }
                })
            }
        }
    }
</script>


<style scoped lang="scss" rel="stylesheet/scss">

</style>
