<template>
    <div>
        <geometry-element
                :selectable="movingElement"
                :movable="!isRead && movingElement"
                :resizable="!isRead && movingElement"
                :deletable="!isRead"
                connectable
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                :angle.sync="value.elementView.angle"
                :customMoveActionExist="isCustomMoveActionExist"
                v-on:customMoveAction="delayedMove"

                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"

                v-on:dblclick="showProperty()"
                v-on:rotateShape="onRotateShape"
                v-on:labelChanged="onLabelChanged"
                v-on:addedToGroup="onAddedToGroup"
                v-on:removeShape="onRemoveShape(value)"
                :label.sync="namePanel"
                :image.sync="setImg"
                :_style="{
                'label-angle':value.elementView.angle,
                'font-weight': 'bold','font-size': '16'
                }"
        >
            <!--v-on:dblclick="$refs['dialog'].open()"-->
            <geometry-rect
                    v-if="movingElement"
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#F1A746',
          'fill': '#F1A746',
          'fill-opacity': 1,
          'r': '1'
        }">
            </geometry-rect>

            <geometry-rect
                    v-else
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 0,
          'stroke': '#F1A746',
          'fill': '#F1A746',
          'fill-opacity': .5,
          'r': '1'
        }">
            </geometry-rect>

            <sub-elements v-for="(index) in newEditUserImg.length">
                <image-element
                        v-bind:image="newEditUserImg[index-1].picture"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="(10*(index-1))+'px'"
                        :sub-bottom="value.elementView.height"
                >
                </image-element>
            </sub-elements>

            <sub-elements>


                <image-element
                        v-if="alertImage"
                        v-bind:image="failed_image"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="'5px'"
                        :sub-bottom="'5px'"
                >
                </image-element>


                <text-element
                        :sub-width="'100%'"
                        :sub-height="titleH"
                        :sub-top="0"
                        :sub-left="0"
                        :text="value.classReference ? value.classReference : '<< Event >>'">
                </text-element>

            </sub-elements>
        </geometry-element>

        <modeling-property-panel
                v-if="openPanel"
                v-model="value"
                titleName="Event"
                :errorMsg="errorMsg"
                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/event.png'"
        >
        </modeling-property-panel>

    </div>
</template>

<script>
    import Element from '../../modeling/Element'

    var changeCase = require('change-case');
    var pluralize = require('pluralize');

    var Mustache = require('mustache')
    import * as _ from 'lodash';

    export default {
        mixins: [Element],
        name: 'domain-event-definition',
        props: {},
        computed: {
            errorMsg() {
                var me = this
                if (me.namePanel == '' && Object.keys(me.value.aggregate).length == 0 && Object.keys(me.value.boundedContext).length == 0) {
                    return 'Element must be in a Bounded Context, Must be attached to an Aggregate and Must has a name.'
                } else if (Object.keys(me.value.aggregate).length == 0) {
                    return 'Must be attached to an Aggregate.'
                } else if (Object.keys(me.value.boundedContext).length == 0) {
                    return 'Element must be in a Bounded Context.'
                } else if (me.namePanel == '') {
                    return 'Must has a name.'
                } else {
                    return ''
                }
            },
            alertImage() {
                var me = this
                if ((me.value.aggregate == {}) || me.namePanel == '' || me.value.boundedContext == {}) {
                    return true
                } else {
                    return false
                }
            },
            bodyHe() {
                return this.value.elementView.height
            },
            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.modeling.model.Event'
            },
            createNew(elementId, x, y, width, height, angle) {
                return {
                    _type: this.className(),
                    name: '',
                    namePascalCase: '',
                    nameCamelCase: '',
                    namePlural: '',

                    aggregate: {},
                    boundedContext: {},
                    fieldDescriptors: [
                        {
                            _type: "org.uengine.model.FieldDescriptor",
                            name: "id",
                            className: "Long",
                            nameCamelCase: 'id',
                            namePascalCase: 'Id',
                            isKey: true
                        }
                    ],
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': 100,
                        'height': 100,
                        'style': JSON.stringify({}),
                        'angle': 0,
                    },
                    alertURL: location.pathname + ((location.pathname == '/' || location.pathname.lastIndexOf('/') > 0) ? '' : '/') + 'static/image/symbol/alert-icon.png',
                    checkAlert: true,
                    relationPolicyInfo: [],
                    relationCommandInfo: [],
                    trigger: '@PrePersist',

                    // isUndoRedo: false,

                }
            },

        },
        data: function () {
            return {
                isRead: false,
                itemH: 20,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference,
                isValueChanged: false,
                newEditUserImg: [],
                setImg: '',
            };
        },
        created: function () {
            var me = this
            var designer = me.getComponent('modeling-designer')

            this.$nextTick(function () {
                if (this.namePanel != null) {
                    this.namePanel = me.value.name
                }

                me.isRead = designer.isRead

            })

        },
        watch: {
            "movingElement": function () {
                this.refreshImg()
            },
            "alertImage": function () {
                this.refreshImg()
            },
            "newEditUserImg": function (newVal) {
                var me = this
                me.$nextTick(function () {
                    if (newVal.length != 0) {
                        this.setImg = newVal[newVal.length - 1].picture
                    } else {
                        this.setImg = ''
                    }

                })
            },
            "value.name": function (newVal, oldVal) {
                this.namePanel = newVal
                this.refreshImg()
            },
            "value.aggregate": {
                deep: true,
                handler: function (newVal, oldVal) {
                    var me = this
                    if (Object.keys(newVal).length == 0) {
                        //delete agg

                    } else {
                        if (newVal.id == oldVal.id) {
                            // console.log("Equal")
                        } else {
                            this.isValueChanged = true
                            // console.log("no Equal")
                            var addObj = {
                                state: 'addEvent',
                                element: JSON.parse(JSON.stringify(me.value))
                            }
                            var deleteObj = {
                                state: "deleteEvent",
                                element: JSON.parse(JSON.stringify(me.value))
                            }

                            if (oldVal.id != null) {
                                me.$EventBus.$emit(oldVal.id, deleteObj)
                            }
                            me.$EventBus.$emit(newVal.id, addObj)

                            //init agg entity
                            if (me.value.fieldDescriptors.length == 0) {
                                me.value.fieldDescriptors = JSON.parse(JSON.stringify(newVal.fieldDescriptors))
                            }
                            me.refreshImg()
                        }
                    }

                }
            },
            "value.boundedContext": function () {
                var me = this
                var updateObj = {
                    state: "updateEvent",
                    element: JSON.parse(JSON.stringify(me.value))
                }


                if (me.value.relationPolicyInfo.length > 0) {
                    me.value.relationPolicyInfo.forEach(function (po) {
                        me.$EventBus.$emit(`${po.policyValue.elementView.id}`, updateObj)
                    })
                }
                if (me.value.relationCommandInfo.length > 0) {
                    me.value.relationCommandInfo.forEach(function (co) {
                        me.$EventBus.$emit(`${co.commandValue.elementView.id}`, updateObj)
                    })
                }

                if (Object.keys(me.value.aggregate).length > 0) {
                    me.$EventBus.$emit(`${me.value.aggregate.id}`, updateObj)
                }

                this.refreshImg()
            },
            "openPanel": function (newVal) {
                var me = this

                if (newVal == false) {

                    me.$nextTick(function () {

                        if (me.isValueChanged) {
                            var obj = {
                                state: "updateEvent",
                                element: JSON.parse(JSON.stringify(me.value))
                            }

                            // //aggregate notify
                            // if (me.value.aggregate.id != null) {
                            //     me.$EventBus.$emit(`${me.value.aggregate.id}`, obj)
                            // }

                            //policy notify
                            if (me.value.relationPolicyInfo.length > 0) {
                                me.value.relationPolicyInfo.forEach(function (policy) {
                                    console.log('1: ', policy)
                                    me.$EventBus.$emit(`${policy.policyValue.elementView.id}`, obj)
                                })
                            }

                            // Command notiyfy
                            if (me.value.relationCommandInfo.length > 0) {
                                me.value.relationCommandInfo.forEach(function (command) {
                                    me.$EventBus.$emit(`${command.commandValue.elementView.id}`, obj)
                                })
                            }

                            if (me.value.relationCommandInfo.length == 0 && me.value.relationPolicyInfo.length == 0) {
                                if (me.value.aggregate.id != null) {
                                    me.$EventBus.$emit(`${me.value.aggregate.id}`, obj)
                                }
                            }

                            me.isValueChanged = false
                        } else {

                        }

                    })


                } else {
                    var designer = me.getComponent('modeling-designer')
                    designer.$refs[`${me.value.elementView.id}`][0].namePanel
                }

            },

        },
        mounted: function () {
            var me = this
            var designer = me.getComponent('modeling-designer')


            me.$nextTick(function () {
                me.isRead = designer.isRead
            })

            this.$EventBus.$on(`${me.value.elementView.id}`, function (obj) {

                if (obj.state == 'deleteRelation') {

                    if (obj.element.targetElement._type == 'org.uengine.modeling.model.Policy') {


                        var targetId = obj.element.targetElement.elementView.id
                        var findPolicyIndex = me.value.relationPolicyInfo.findIndex(policyInfo => policyInfo.policyValue.elementView.id === targetId)

                        if (findPolicyIndex != -1) {
                            me.value.relationPolicyInfo[findPolicyIndex] = null
                        }

                        me.value.relationPolicyInfo = me.value.relationPolicyInfo.filter(n => n)


                    } else {
                        var targetId = obj.element.targetElement.elementView.id
                        var findCommandIndex = me.value.relationCommandInfo.findIndex(policyInfo => policyInfo.commandValue.elementView.id === targetId)

                        if (findCommandIndex != -1) {
                            me.value.relationCommandInfo[findCommandIndex] = null
                        }

                        me.value.relationCommandInfo = me.value.relationCommandInfo.filter(n => n)
                    }

                    var updateObj = {
                        state: "updateEvent",
                        element: JSON.parse(JSON.stringify(me.value))
                    }

                    if (Object.keys(me.value.aggregate).length > 0) {
                        me.$EventBus.$emit(`${me.value.aggregate.id}`, updateObj)
                    }


                } else if (obj.state == 'updateAggregate') {

                    var tmpAgg = {
                        'id': obj.element.elementView.id,
                        'name': obj.element.name,
                        'namePlural': obj.element.namePlural,
                        'namePascalCase': obj.element.namePascalCase,
                        'nameCamelCase': obj.element.nameCamelCase,
                        'fieldDescriptors': obj.element.aggregateRoot.fieldDescriptors,
                        'boundedContext': obj.element.boundedContext,
                    }

                    me.value.aggregate = tmpAgg

                    var updateObj = {
                        state: "updateEvent",
                        element: JSON.parse(JSON.stringify(me.value))
                    }

                    if (Object.keys(me.value.aggregate).length > 0) {
                        me.$EventBus.$emit(`${me.value.aggregate.id}`, updateObj)
                    }

                    if (me.value.relationCommandInfo.length > 0) {
                        me.value.relationCommandInfo.forEach(function (co) {
                            me.$EventBus.$emit(`${co.commandValue.elementView.id}`, updateObj)
                        })
                    }
                    if (me.value.relationPolicyInfo.length > 0) {
                        me.value.relationPolicyInfo.forEach(function (po) {
                            me.$EventBus.$emit(`${po.policyValue.elementView.id}`, updateObj)
                        })
                    }

                } else if (obj.state == 'deleteAggregate') {
                    me.value.aggregate = {}

                } else if (obj.state == 'updateCommand') {

                    var targetId = obj.element.elementView.id
                    var findCommandIndex = me.value.relationCommandInfo.findIndex(co => co.commandValue.elementView.id == targetId)

                    if (findCommandIndex != -1) {
                        me.value.relationCommandInfo[findCommandIndex].commandValue = obj.element
                    }

                    if (Object.keys(me.value.aggregate).length > 0) {
                        var updateObj = {
                            state: "updateEvent",
                            element: JSON.parse(JSON.stringify(me.value))
                        }
                        me.$EventBus.$emit(`${me.value.aggregate.id}`, updateObj)
                    }

                } else if (obj.state == 'updatePolicy') {

                    //relation    .
                    var targetId = obj.element.elementView.id
                    var findPolicyIndex = me.value.relationPolicyInfo.findIndex(po => po.policyValue.elementView.id == targetId)


                    if (findPolicyIndex != -1) {
                        me.value.relationPolicyInfo[findPolicyIndex].policyValue = obj.element

                        var updateObj = {
                            state: "updateEvent",
                            element: JSON.parse(JSON.stringify(me.value))
                        }

                        if (Object.keys(me.value.aggregate).length > 0) {
                            me.$EventBus.$emit(`${me.value.aggregate.id}`, updateObj)
                        } else {

                            if (me.value.relationPolicyInfo[findPolicyIndex].policyValue.boundedContext != {}) {
                                me.$EventBus.$emit(`${me.value.relationPolicyInfo[findPolicyIndex].policyValue.boundedContext.id}`, updateObj)
                            }
                        }
                    }


                } else if (obj.state == 'updateBounded') {

                    me.value.boundedContext.name = obj.element.name;
                    me.value.boundedContext.id = obj.element.elementView.id;


                    var updateObj = {
                        state: "updateEvent",
                        element: JSON.parse(JSON.stringify(me.value))
                    }


                    if (me.value.relationPolicyInfo.length > 0) {
                        me.value.relationPolicyInfo.forEach(function (po) {
                            me.$EventBus.$emit(`${po.policyValue.elementView.id}`, updateObj)
                        })
                    }
                    if (me.value.relationCommandInfo.length > 0) {
                        me.value.relationCommandInfo.forEach(function (co) {
                            me.$EventBus.$emit(`${co.commandValue.elementView.id}`, updateObj)
                        })
                    }

                    if (Object.keys(me.value.aggregate).length > 0) {
                        me.$EventBus.$emit(`${me.value.aggregate.id}`, updateObj)
                    }

                } else if (obj.state == 'elementMove') {
                    me.movingElement = true
                    var afterMove = JSON.parse(obj.element)
                    me.value.elementView.x = afterMove.x
                    me.value.elementView.y = afterMove.y
                    me.value.elementView.width = afterMove.width
                    me.value.elementView.height = afterMove.height
                }


                if (obj.state == 'editUserPanelOpen' || obj.state == 'editUserSelectOn') {

                    if (me.newEditUserImg.findIndex(user => user.uid == obj.uid) == -1) {
                        me.newEditUserImg.push(obj)
                    }

                } else if (obj.state == 'editUserPanelClose' || obj.state == 'editUserSelectOff') {
                    if (me.newEditUserImg.length > 0) {
                        me.newEditUserImg.forEach(function (user, idx) {
                            if (user.uid == obj.uid) {
                                me.newEditUserImg[idx] = null
                            }
                        })
                        me.newEditUserImg = me.newEditUserImg.filter(n => n)
                    }
                }
            })
        },

        methods: {
            refreshImg() {
                var me = this
                var origin = JSON.parse(JSON.stringify(this.setImg))
                this.setImg = 'test'
                me.$nextTick(function () {
                    if (this.setImg == 'test') {
                        this.setImg = ''
                    } else {
                        this.setImg = 'test'
                    }
                })
            },
            setNameCase:
                function (newVal) {
                    var me = this
                    me.value.name = newVal
                    me.value.namePascalCase = changeCase.pascalCase(newVal)
                    me.value.nameCamelCase = changeCase.camelCase(newVal)
                    me.value.namePlural = changeCase.camelCase(pluralize(newVal));
                },
            getComponent(componentName) {
                let component = null
                let parent = this.$parent
                while (parent && !component) {
                    if (parent.$options.name === componentName) {
                        component = parent
                    }
                    parent = parent.$parent
                }
                return component
            },

            deleteEvent() {
                var me = this
                return new Promise(function (resolve) {

                    me.$EventBus.$off(`${me.value.elementView.id}`);

                    var obj = {
                        state: "deleteEvent",
                        element: JSON.parse(JSON.stringify(me.value))
                    }

                    //aggregate notify
                    if (me.value.aggregate.id != null) {
                        me.$EventBus.$emit(`${me.value.aggregate.id}`, obj)
                    }
                    if (me.value.boundedContext.id != null) {
                        me.$EventBus.$emit(`${me.value.boundedContext.id}`, obj)
                    }
                    //policy && relation notify
                    if (me.value.relationPolicyInfo.length > 0) {
                        me.value.relationPolicyInfo.forEach(function (poItem) {
                            //policy
                            me.$EventBus.$emit(`${poItem.policyValue.elementView.id}`, obj)
                            //relation
                            me.$EventBus.$emit(`${poItem.relationId}`, obj)
                        })
                    }

                    // var designer = me.getComponent('modeling-designer')
                    // console.log(designer.$refs[me.value.elementView.id] )
                    me.$emit('update:value', null);
                    resolve()
                })
            }
        }
    }
</script>


<style scoped lang="scss" rel="stylesheet/scss">
</style>
