<template>
    <v-container fluid>
        <div  v-if="!isAdmin">  . </div>
        <v-row v-else>
            <v-col v-for="(lab, index) in sortedLabsList" :key="index" md="3">
                <v-card style="width: 400px">
                    <!--                    <img v-if="a.imgSrc"-->
                    <!--                         :src="labs.imgSrc"-->
                    <!--                         style="width: 300px; height: 150px"-->
                    <!--                    >-->
                    <v-card-title>
                        {{lab.labName}}
                    </v-card-title>
                    <v-card-text>
                         : {{lab.labScenario}} <br>
                         : {{lab.labTime}}
                    </v-card-text>
                    <v-card-actions>

                        <v-spacer></v-spacer>
                        <v-btn :to="$route.fullPath+ '/labs/' + lab.labName"> </v-btn>
                    </v-card-actions>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    var Minio = require('minio');

    export default {
        name: "ClazzListPage",

        components: {},

        data: () => ({
            labsList: {},
            overlay: false,
            minioClient: new Minio.Client({
                endPoint: "35.232.244.51",
                port: 9000,
                useSSL: false,
                accessKey: "minio",
                secretKey: "minio123"
            }),
            isAdmin: false
        }),
        computed: {
            sortedLabsList() {
                var me = this
                var o = me.labsList
                var sorted = {},
                    key, a = [];
                //    
                for (key in o) {
                    if (o.hasOwnProperty(key)) a.push(key);
                }
                //   
                a.sort();
                //     object 
                for (key = 0; key < a.length; key++) {
                    sorted[a[key]] = o[a[key]];
                }
                return sorted;
            }
        },
        watch: {},
        mounted() {
            var me = this
            this.getAdminLabsList();
            if(window.localStorage.getItem('authorized') == 'admin') {
                me.isAdmin = true
            }
        },
        methods: {
            getAdminLabsList() {
                var me = this
                var path = this.$route.params.course + '/runningClass/' + this.$route.params.clazzName + '/'
                console.log(path)
                var rootStream = this.minioClient.listObjects('labs', path, false)
                rootStream.on('data', function (obj) {
                    if(obj.prefix  && obj.size == 0) {
                        //   

                        var labMetaPath = obj.prefix.replace('runningClass', 'planed').replace(me.$route.params.clazzName + '/', '')
                        var labsStream = me.minioClient.listObjects('labs', labMetaPath, false)
                        labsStream.on('data', function (labsObj) {
                            if(labsObj.name.includes('Metadata')) {
                                me.minioClient.getObject('labs', labsObj.name, function (err, labDataStream) {
                                    if (err) {
                                        return console.log(err)
                                    }
                                    labDataStream.on('data', function (labChunk) {
                                        var string = new TextDecoder("utf-8").decode(labChunk);
                                        var json = JSON.parse(string)
                                        // me.labsList.push(json)
                                        me.$set(me.labsList, labsObj.name.split('/')[2], json)
                                    })
                                })
                            }
                        })
                    }
                })
            },
        }
    };
</script>
