<template>
    <v-container fluid>
        <h1>Class</h1>
        <v-overlay :value="overlay">
            <v-btn
                    icon
                    @click="overlay = false"
            >
                <v-progress-circular
                        :size="50"
                        color="primary"
                        indeterminate
                ></v-progress-circular>
                <v-icon>mdi-close</v-icon>
            </v-btn>
        </v-overlay>
        <v-row>
            <v-col v-if="loadingStatus" align="center" justify="center">
                <v-progress-circular
                        :size="50"
                        color="primary"
                        indeterminate
                ></v-progress-circular>
            </v-col>
            <v-col v-for="(course, index) in sortedClazzList" :key="index" md="3">
                <v-card v-if="course['availableUserList'].indexOf(email) != -1">
                    <v-img :src="course.imgSrc"></v-img>
                    <v-card-title>
                        <!--                        {{course}}-->
                          : {{course.course}}
                    </v-card-title>
                    <v-card-text>
                          : {{course.className}}<br>
                          : {{course.classStartDate}} <br>
                          : {{course.classEndDate}} <br>
                          : {{course.availableUserList.length}} 
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn text
                               :to="'/student/'+course.course + '/' + course.className">
                            
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    var Minio = require('minio');

    export default {
        name: "ClazzListPage",
        components: {},
        data: () => ({
            clazzList: {},
            overlay: false,
            minioClient: new Minio.Client({
                endPoint: "35.232.244.51",
                port: 9000,
                useSSL: false,
                accessKey: "minio",
                secretKey: "minio123",
            }),
            email: ''
        }),
        computed: {
            loadingStatus() {
                if (Object.keys(this.clazzList).length < 1) {
                    return true
                } else {
                    return false
                }
            },
            sortedClazzList() {
                var me = this
                var o = me.clazzList;
                var sorted = {},
                    key, a = [];
                //    
                for (key in o) {
                    if (o.hasOwnProperty(key)) a.push(key);
                }
                //   
                a.sort();
                //     object 
                for (key = 0; key < a.length; key++) {
                    sorted[a[key]] = o[a[key]];
                }
                return sorted;
            }
        },
        watch: {},
        mounted() {
            var me = this
            this.email = localStorage.getItem("email");
            if (this.email)
                this.getClazzList();
        },
        methods: {
            getClazzList() {
                var me = this
                var stream = this.minioClient.listObjects('labs', '', true);
                stream.on('data', function (obj) {
                    if (obj.name.includes('Class_Metadata') && obj.size != 0) {
                        me.minioClient.getObject('labs', obj.name, function (err, dataStream) {
                            if (err) {
                                return console.log(err)
                            }
                            dataStream.on('data', function (chunk) {
                                var string = new TextDecoder("utf-8").decode(chunk);
                                var json = JSON.parse(string)
                                json["path"] = obj.name.replace('Class_Metadata.json', '')
                                var thumbnailURL = obj.name.replace('Class_Metadata.json', 'thumbnail.png')
                                me.minioClient.getObject('labs', thumbnailURL, function (err, dataStream) {
                                    if (err) {
                                        return console.log(err)
                                    }
                                    dataStream.on('data', function (chunk) {
                                        var base64Data = btoa(String.fromCharCode.apply(null, chunk))
                                        json['imgSrc'] = 'data:image/png;base64,' + base64Data
                                        json['course'] = obj.name.split('/')[0];
                                        me.$set(me.clazzList, obj.name.split('/')[0] + '/' + obj.name.split('/')[2], json)
                                    })
                                    dataStream.on('error', function (err) {
                                        console.log(err)
                                    })
                                })
                            })
                            dataStream.on('error', function (err) {
                                console.log(err)
                            })
                        })
                    }
                })
                stream.on('error', function (err) {
                    console.log(err)
                })
            },
        }
    };
</script>
