<template>
    <div>
        <edge-element
                v-if="value.sourceElement._type == 'org.uengine.modeling.model.Event' && value.targetElement._type != 'org.uengine.modeling.model.Event'"
                selectable
                connectable
                deletable
                :vertices.sync="vertices"
                :from.sync="value.from"
                :to.sync="value.to"
                :_style="style_"
                :label="value.name"
                v-on:dblclick="showProperty"
                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:removeShape="onRemoveShape(value)"

                :customMoveActionExist="isCustomMoveActionExist"
                v-on:customRelationMoveAction="delayedRelationMove"
        >
        </edge-element>
    </div>
</template>

<script>
    import Element from '../../modeling/Element'
    import UmlPropertyPanel from "../../modeling/UMLPropertyPanel";

    export default {
        mixins: [Element],
        name: 'class-relation',
        props: {
            value: Object
        },

        created: function () {
            var me = this
            if (this.value && this.value.relationView) {
                this.value.from = this.value.relationView.from;
                this.value.to = this.value.relationView.to;
            }

        },
        computed: {
            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.modeling.model.Relation'
            },
            style_() {
                var style = {}
                if (this.value.targetElement._type == 'org.uengine.modeling.model.Policy') {
                    style = {
                        "arrow-end": "block",
                        'stroke-width': '1.3',
                        'stroke-dasharray': '- ',
                        'font-size': 20,
                    }
                } else if (this.value.targetElement._type == 'org.uengine.modeling.model.Command') {
                    style = {
                        "arrow-end": "block",
                        'font-size': 20,
                    }
                }

                return style
            },
            createNew(elementId, from, to, vertices) {
                return {
                    _type: this.className(),
                    name: '',
                    sourceElement: from,
                    targetElement: to,
                    from: from.elementView.id,
                    to: to.elementView.id,
                    relationView: {
                        id: elementId,
                        style: JSON.stringify({
                            "arrow-start": "none",
                            "arrow-end": "none",
                        }),
                        value: vertices,
                        from: from.elementView.id,
                        to: to.elementView.id,
                        needReconnect: true,
                    },
                    sourceMultiplicity: 1,
                    targetMultiplicity: 1,
                }
            },
            vertices: {
                get: function () {
                    var style;
                    try {
                        return JSON.parse(this.value.relationView.value);
                    } catch (e) {
                        return null;
                    }
                },
                set: function (val) {
                    this.value.relationView.value = JSON.stringify(val);
                }
            }
        },
        data: function () {
            return {}
        },
        watch: {},
        mounted: function () {
            var me = this

            this.$EventBus.$on(`${me.value.relationView.id}`, function (obj) {
                if (obj.state == 'deleteEvent') {
                    me.deleteRelation()
                }
                if (obj.state == 'deletePolicy') {
                    me.deleteRelation()
                }
                if (obj.state == 'deleteCommand') {
                    me.deleteRelation()
                }
                if (obj.state == 'deleteRelation') {
                    me.deleteRelation()
                }

                if (obj.state == 'relationMove') {
                    console.log('asdasd')
                    me.value.relationView.value = obj.element
                }

            })



            // var obj = {
            //     state: "addRelation",
            //     element: JSON.parse(JSON.stringify(me.value))
            // }
            //
            // me.$EventBus.$emit(me.value.sourceElement.elementView.id, obj)
            // me.$EventBus.$emit(me.value.targetElement.elementView.id, obj)

        },
        methods: {
            deleteRelation() {
                var me = this

                return new Promise(function (resolve) {
                    // console.log("deleteRelation")
                    //evnet, policy   
                    me.$EventBus.$off(`${me.value.relationView.id}`);

                    var obj = {
                        state: "deleteRelation",
                        element: JSON.parse(JSON.stringify(me.value))
                    }
                    if (me.value.from) {
                        me.$EventBus.$emit(me.value.from, obj)

                    }
                    if (me.value.to) {
                        me.$EventBus.$emit(me.value.to, obj)
                    }

                    me.$emit('update:value', null);
                    resolve()
                })
            }
        }
    }
</script>


<style scoped lang="scss" rel="stylesheet/scss">

