<template>
    <div>
        <geometry-element
                :selectable="movingElement"
                :movable="!isRead || movingElement"
                :resizable="!isRead || movingElement"
                :deletable=!isRead
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                :angle.sync="value.elementView.angle"
                :customMoveActionExist="isCustomMoveActionExist"
                v-on:customMoveAction="delayedMove"
                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:dblclick="showProperty"
                v-on:addedToGroup="onAddedToGroup"
                v-on:removeShape="onRemoveShape(value)"
                :label="namePanel"
                :image.sync="setImg"
                :_style="{
                'label-angle':value.elementView.angle,
                'font-weight': 'bold','font-size': '16'
                }"
        >
            <!--v-on:dblclick="$refs['dialog'].open()"-->
            <geometry-rect
                    v-if="movingElement"
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#F8D454',
          fill: '#F8D454',
          'fill-opacity': 1,
          r: '1'
        }"
            >
            </geometry-rect>

            <geometry-rect
                    v-else
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#F8D454',
          fill: '#F8D454',
          'fill-opacity': .5,
          r: '1'
        }"
            >
            </geometry-rect>

            <sub-elements v-for="(index) in newEditUserImg.length">
                <image-element
                        v-bind:image="newEditUserImg[index-1].picture"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="(10*(index-1))+'px'"
                        :sub-bottom="value.elementView.height"
                >
                </image-element>
            </sub-elements>

            <sub-elements>
                <!--title-->
                <text-element
                        :sub-width="'100%'"
                        :sub-height="titleH"
                        :sub-top="0"
                        :sub-left="0"
                        :text="value.classReference ? value.classReference : '<< Aggregate >>'">
                </text-element>
                <image-element
                        v-if="alertImage"
                        v-bind:image="failed_image"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="'5px'"
                        :sub-bottom="'5px'"
                >
                </image-element>
            </sub-elements>
        </geometry-element>


        <!--        <modeling-property-panel-->
        <!--                v-model="value"-->
        <!--                :titleName="'Aggregate'"-->
        <!--                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/aggregate.png'"-->
        <!--        >-->
        <!--        </modeling-property-panel>-->

        <modeling-property-panel
                v-if="openPanel"
                v-model="value"
                :titleName="'Aggregate'"
                :errorMsg="errorMsg"
                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/aggregate.png'"
        >
        </modeling-property-panel>

    </div>
</template>

<script>
    import Element from '../../modeling/Element'

    // var Mustache = require('mustache')

    export default {
        mixins: [Element],
        name: 'aggregate-definition',
        props: {},
        computed: {
            errorMsg() {
                var me = this

                if (me.namePanel == '' && Object.keys(me.value.boundedContext).length == 0) {
                    return 'Element must be in a Bounded Context and Must has a name.'
                } else if (Object.keys(me.value.boundedContext).length == 0) {
                    return 'Element must be in a Bounded Context.'
                } else if (me.namePanel == '') {
                    return 'Must has a name.'
                } else {
                    return ''
                }
            },
            alertImage() {
                var me = this
                if ((Object.keys(me.value.boundedContext).length === 0 || me.namePanel == '')) {
                    return true
                } else {
                    return false
                }
            },
            namePascalCase() {
                var me = this
                return me.name.charAt(0).toUpperCase() + me.name.slice(1)
            },
            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.modeling.model.Aggregate'
            },
            createNew(elementId, x, y, width, height) {
                return {
                    _type: this.className(),
                    name: '',
                    namePlural: '',
                    namePascalCase: '',
                    nameCamelCase: '',
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': 100,
                        'height': 100,
                        'style': JSON.stringify({})
                    },
                    // checkValue: false,
                    // drawer: false,
                    // selected: false,
                    boundedContext: {},
                    aggregateRoot: {
                        _type: "org.uengine.modeling.model.AggregateRoot",
                        fieldDescriptors:
                            [
                                {
                                    _type: "org.uengine.model.FieldDescriptor",
                                    name: "id",
                                    className: "Long",
                                    nameCamelCase: 'id',
                                    namePascalCase: 'Id',
                                    isKey: true
                                }
                            ],
                    },
                    events: [],
                    commands: [],
                }

            },
            computedElement() {
                return _.cloneDeep(this.value.aggregateRoot.fieldDescriptors)
            }
        },
        data: function () {
            return {
                isRead: false,
                itemH: 200,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference,
                isValueChanged: false,
                newEditUserImg: [],
                setImg: '',
            };
        },
        created: function () {
            var me = this
            var designer = me.getComponent('modeling-designer')

            this.$nextTick(function () {
                if (this.namePanel != null) {
                    this.namePanel = me.value.name
                }

                me.isRead = designer.isRead
            })
        },
        watch: {
            "movingElement": function () {
                this.refreshImg()
            },
            "alertImage": function () {
                this.refreshImg()
            },
            "newEditUserImg": function (newVal) {
                var me = this
                me.$nextTick(function () {
                    if (newVal.length != 0) {
                        this.setImg = newVal[newVal.length - 1].picture
                    } else {
                        this.setImg = ''
                    }

                })
            },

            "value.name": function (newVal, oldVal) {
                this.namePanel = newVal
            },
            "value.boundedContext": {
                deep: true,
                handler: function () {
                    this.checkValue = true
                }
            },
            "openPanel": function (newVal) {
                var me = this
                if (newVal == false) {

                    me.$nextTick(function () {
                        if (me.isValueChanged) {
                            var obj = {
                                state: 'updateAggregate',
                                element: JSON.parse(JSON.stringify(me.value))
                            }
                            //command, event, policy 

                            if (me.value.boundedContext.id != null) {
                                me.$EventBus.$emit(me.value.boundedContext.id, obj)
                            }

                            if (me.value.events.length > 0)
                                me.value.events.forEach(function (eve) {
                                    me.$EventBus.$emit(`${eve.elementView.id}`, obj)
                                    // eve.aggregate = tmpAgg
                                    // eve.fieldDescriptors = me.value.aggregateRoot.fieldDescriptors
                                })

                            if (me.value.commands.length > 0)
                                me.value.commands.forEach(function (co) {
                                    me.$EventBus.$emit(`${co.elementView.id}`, obj)
                                    // co.aggregate = tmpAgg
                                })
                            me.isValueChanged = false
                        } else {

                        }

                    })

                    me.checkValue = false

                }

            },
        },
        mounted: function () {
            var me = this
            var designer = me.getComponent('modeling-designer')

            this.$nextTick(function () {
                this.namePanel = me.value.name

                me.isRead = designer.isRead
            })


            me.$EventBus.$on(`${me.value.elementView.id}`, function (obj) {
                if (obj.state.includes('Bounded')) {

                    if (obj.state == 'updateBounded') {
                        me.value.boundedContext.name = obj.element.name;
                        me.value.boundedContext.id = obj.element.elementView.id;

                        //aggregate connection elements
                        if (me.value.events.length > 0)
                            me.value.events.forEach(function (eve) {
                                eve.boundedContext.name = obj.element.name;
                                eve.boundedContext.id = obj.element.elementView.id;
                            })

                        if (me.value.commands.length > 0)
                            me.value.commands.forEach(function (co) {
                                co.boundedContext.name = obj.element.name;
                                co.boundedContext.id = obj.element.elementView.id;
                            })


                        var aggObj = {
                            state: "updateAggregate",
                            element: JSON.parse(JSON.stringify(me.value))
                        }
                        me.$EventBus.$emit(me.value.boundedContext.id, aggObj)
                    }


                } else {

                    me.managedAggregate(obj)
                    // me.managedAgg(obj)
                    //     .then(function () {
                    //         if (me.value.boundedContext != null && me.value.boundedContext.id != null) {
                    //             var aggObj = {
                    //                 state: "updateAggregate",
                    //                 element: JSON.parse(JSON.stringify(me.value))
                    //             }
                    //             me.$EventBus.$emit(me.value.boundedContext.id, aggObj)
                    //         }
                    //     })
                }


                if (obj.state == 'editUserPanelOpen' || obj.state == 'editUserSelectOn') {

                    if (me.newEditUserImg.findIndex(user => user.uid == obj.uid) == -1) {
                        me.newEditUserImg.push(obj)
                    }

                } else if (obj.state == 'editUserPanelClose' || obj.state == 'editUserSelectOff') {
                    if (me.newEditUserImg.length > 0) {
                        me.newEditUserImg.forEach(function (user, idx) {
                            if (user.uid == obj.uid) {
                                me.newEditUserImg[idx] = null
                            }
                        })
                        me.newEditUserImg = me.newEditUserImg.filter(n => n)

                    }
                }
            })
        },
        methods: {
            async managedAggregate(obj) {
                var me = this

                await me.managedAgg(obj)


                if (me.value.boundedContext != null && me.value.boundedContext.id != null) {
                    var aggObj = {
                        state: "updateAggregate",
                        element: JSON.parse(JSON.stringify(me.value))
                    }
                    console.log('5: updateAggregate')
                    me.$EventBus.$emit(me.value.boundedContext.id, aggObj)
                }
            },
            refreshImg() {
                var me = this
                var origin = JSON.parse(JSON.stringify(this.setImg))
                this.setImg = 'test'
                me.$nextTick(function () {
                    if (this.setImg == 'test') {
                        this.setImg = ''
                    } else {
                        this.setImg = 'test'
                    }
                })
            },
            difference(object, base) {
                function changes(object, base) {
                    return _.transform(object, function (result, value, key) {
                        if (!_.isEqual(value, base[key])) {
                            result[key] = (_.isObject(value) && _.isObject(base[key])) ? changes(value, base[key]) : value;
                        }
                    });
                }

                return changes(object, base);
            },
            managedAgg(obj) {
                var me = this

                    if (obj.state == 'deleteEvent') {

                        var targetId = obj.element.elementView.id
                        var findEvIndex = me.value.events.findIndex(ev => ev.elementView.id === targetId)

                        if (findEvIndex != -1) {
                            me.value.events[findEvIndex] = null;
                        }
                        me.value.events = me.value.events.filter(n => n)


                    } else if (obj.state == 'addEvent') {

                        var targetId = obj.element.elementView.id
                        var findEvIndex = me.value.events.findIndex(ev => ev.elementView.id == targetId)

                        console.log('addEvent',findEvIndex)
                        if (findEvIndex == -1) {
                            me.value.events.push(obj.element)

                        }else{
                            me.value.events[findEvIndex] = obj.element;
                        }

                    } else if (obj.state == 'updateEvent') {

                        var targetId = obj.element.elementView.id
                        var findEvIndex = me.value.events.findIndex(ev => ev.elementView.id == targetId)

                        if (findEvIndex == -1) {
                            me.value.events.push(obj.element) ;
                        }else{
                            me.value.events[findEvIndex] = obj.element;
                        }

                    } else if (obj.state == 'deleteCommand') {

                        var targetId = obj.element.elementView.id
                        var findEvIndex = me.value.commands.findIndex(ev => ev.elementView.id === targetId)

                        if (findEvIndex != -1) {
                            me.value.commands[findEvIndex] = null;
                        }

                        me.value.commands = me.value.commands.filter(n => n)


                        // me.value.commands.some(function (item, idx) {
                        //     if (item.elementView.id == obj.element.elementView.id) {
                        //         me.value.commands[idx] = null;
                        //         me.value.commands = me.value.commands.filter(n => n)
                        //         resolve()
                        //     }
                        // })
                    } else if (obj.state == 'addCommand') {

                        var targetId = obj.element.elementView.id
                        var findEvIndex = me.value.commands.findIndex(ev => ev.elementView.id == targetId)

                        if (findEvIndex == -1) {
                            me.value.commands.push(obj.element)

                            if (me.value.commands[findEvIndex].aggregate == {} && me.value.commands[findEvIndex].boundedContext != {}) {
                                var addObj = {
                                    state: 'addCommand',
                                    element: obj.element
                                }
                                me.$EventBus.$emit(me.value.commands[findEvIndex].boundedContext.id, addObj)
                            }

                        }else{
                            me.value.commands[findEvIndex] = obj.element;
                        }


                        // me.value.commands.push(obj.element)
                        // resolve()
                    } else if (obj.state == 'updateCommand') {
                        var targetId = obj.element.elementView.id
                        var findEvIndex = me.value.commands.findIndex(ev => ev.elementView.id == targetId)

                        if (findEvIndex != -1) {

                            me.value.commands[findEvIndex] = obj.element;

                        }else{
                            me.value.commands.push(obj.element);
                        }

                    } else if (obj.state == 'elementMove') {
                        me.movingElement = true
                        var afterMove = JSON.parse(obj.element)
                        me.value.elementView.x = afterMove.x
                        me.value.elementView.y = afterMove.y
                        me.value.elementView.width = afterMove.width
                        me.value.elementView.height = afterMove.height
                    }

                    if (obj.state == 'editUserPanelOpen' || obj.state == 'editUserSelectOn') {

                        if (me.newEditUserImg.findIndex(user => user.uid == obj.uid) == -1) {
                            me.newEditUserImg.push(obj)
                        }

                    } else if (obj.state == 'editUserPanelClose' || obj.state == 'editUserSelectOff') {
                        if (me.newEditUserImg.length > 0) {
                            me.newEditUserImg.forEach(function (user, idx) {
                                if (user.uid == obj.uid) {
                                    me.newEditUserImg[idx] = null
                                }
                            })
                            me.newEditUserImg = me.newEditUserImg.filter(n => n)
                        }
                    }


            },
            deleteAggregate() {
                var me = this
                return new Promise(function (resolve) {
                    // console.log("deleteAggregate")

                    var obj = {
                        state: 'deleteAggregate',
                        element: JSON.parse(JSON.stringify(me.value))
                    }

                    if (me.value.boundedContext.id != null)
                        me.$EventBus.$emit(`${me.value.boundedContext.id}`, obj)

                    if (me.value.events.length > 0)
                        me.value.events.forEach(function (ev) {
                            me.$EventBus.$emit(`${ev.elementView.id}`, obj)
                        })

                    if (me.value.commands.length > 0)
                        me.value.commands.forEach(function (co) {
                            me.$EventBus.$emit(`${co.elementView.id}`, obj)
                        })

                    // if (me.value.viewes.length > 0)
                    //     me.value.viewes.forEach(function (vi) {
                    //         me.$EventBus.$emit(`${vi.elementView.id}`, obj)
                    //     })

                    me.$EventBus.$off(`${me.value.elementView.id}`);
                    me.$emit('update:value', null);
                    resolve()
                })
            }
        },
        beforeDestroy() {
        }

    }
</script>


<style scoped lang="scss" rel="stylesheet/scss">

</style>
